$basic: #323232;
$green: #26a53e;
$yellow: #ffb100;

h1, h2, h3, h4, h5, h6 {
  font-family: 'PT Sans Narrow', sans-serif;
  text-transform: uppercase; }

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 100%;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  color: #fff;
  position: relative;
  &_stroke {
    background-image: linear-gradient(45deg, #ffc400 0%, $yellow 100%);
    &::before {
      @include p;
      background-color: $basic;
      top: 2px;
      right: 2px;
      bottom: 2px;
      left: 2px; }
    span {
      position: relative; }
    &:hover {
      &::before {
        opacity: 0; } } }
  &_yellow {
    color: #ffffff;
    font-size: 16px;
    font-weight: 800;
    line-height: 20px;
    text-transform: uppercase;
    letter-spacing: 0.32px;
    position: relative;
    transition: none;
    background-image: linear-gradient(45deg, #ffc400 0%, $yellow 100%);
    &::before, &::after {
      @include p;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 0; }
    &::before {
      background-image: linear-gradient(45deg, #ffc400 0%, $yellow 100%);
      box-shadow: 0 9px 29px 2px rgba(255, 177, 0, 0.3); }
    &::after {
      background-image: linear-gradient(45deg, #ffba00 0%, #ffa800 100%);
      box-shadow: 0 9px 29px 2px rgba(255, 177, 0, 0.45);
      opacity: 0; }
    span {
      position: relative;
      z-index: 5; }
    &:hover {
      &::before {
        opacity: 0; }
      &::after {
        opacity: 1; } } }
  &_green {
    background: $green;
    &:hover {
      background: #229438; } }
  &_price {
    padding-left: 30px;
    &::before {
      @include p;
      width: 20px;
      height: 20px;
      background: url('../img/sprite.svg#list') no-repeat;
      background-size: 20px;
      top: 50%;
      transform: translateY(-50%);
      left: 21px;
      opacity: .9; } }
  &_custom {
    width: 270px;
    height: 60px; } }

