.menu {
  & > ul {
    display: flex;
    & > li {
      flex-grow: 1;
      position: relative;
      text-align: center;
      &.menu-item-has-children {
        &::after {
          @include p;
          top: 21px;
          right: 23px;
          background: url('../img/sprite.svg#arrow-right') no-repeat center;
          background-size: 9px;
          width: 9px;
          height: 9px;
          z-index: 30;
          @include tr; } }
      &.menu-item-has-children::before {
        @include p;
        top: 0;
        right: 0;
        bottom: -1px;
        left: 0;
        background: #fff;
        border-bottom: 1px solid #e4e4e4;
        z-index: 20;
        opacity: 0;
        @include tr; }
      &:hover {
        &::before {
          opacity: 1; }
        & > ul {
          display: block;
          opacity: 1;
          z-index: 10; }
        &::after {
          transform: rotate(450deg); }
        & > a {
          color: $green; } }
      & > a {
        color: #303030;
        font-weight: 700;
        line-height: 15px;
        letter-spacing: 0.4px;
        line-height: 50px;
        display: block;
        position: relative;
        z-index: 50; }
      & > ul {
        // display: none
        position: absolute;
        left: 0;
        top: 100%;
        background: #fff;
        text-align: left;
        z-index: 10;
        border-radius: 0 0 5px 5px;
        box-shadow: 0 5px 8px 5px rgba(50, 50, 50, 0.07);
        width: 100%;
        opacity: 0;
        z-index: -10;
        @include tr;
        & > li {
          position: relative;
          &.menu-item-has-children::after {
            @include p;
            top: 16px;
            right: 16px;
            background: url('../img/sprite.svg#arrow-right') no-repeat center;
            background-size: 9px;
            width: 9px;
            height: 9px;
            z-index: 30;
            @include tr;
            &:hover {
              &::after {
                transform: rotate(450deg); } } }
          & > a {
            display: block;
            color: $basic;
            font-size: 13px;
            font-weight: 700;
            line-height: 15px;
            letter-spacing: 0.33px;
            line-height: 40px;
            border-bottom: 1px solid #e4e4e4;
            padding: 0 16px;
            &:hover {
              color: $green; } }
          &:last-child {
            & > a {
              border-bottom: 0; } }
          &:hover {
            & > ul {
              display: block; } }
          & > ul {
            position: absolute;
            left: 100%;
            top: 0;
            background: #fff;
            text-align: left;
            z-index: 10;
            border-radius: 0 0 5px 5px;
            box-shadow: 0 5px 8px 5px rgba(50, 50, 50, 0.07);
            // width: 100%
            display: none;
            & > li {
              & > a {
                display: block;
                color: $basic;
                font-size: 13px;
                font-weight: 700;
                line-height: 15px;
                letter-spacing: 0.33px;
                line-height: 40px;
                border-bottom: 1px solid #e4e4e4;
                padding: 0 16px;
                white-space: nowrap;
                &:hover {
                  color: $green; } }
              &:last-child {
                & > a {
                  border-bottom: 0; } } } } } } } } }

