    justify-content: space-between;
    &__input, &__textarea {
      background-color: #f1f1f1;
      letter-spacing: 0.38px;
      font-size: 15px;
      &::placeholder {
        color: #969696; } }
    &__input {
      width: calc(50% - 5px);
      height: 45px;
      padding: 0 20px;
      margin-bottom: 10px; }
    &__textarea {
      width: 100%;
      height: 90px;
      resize: none;
      padding: 12px 20px; }
    &__privacy {
      font-size: 13px;
      font-weight: 300;
      line-height: 20px;
      letter-spacing: 0.33px;
      padding-left: 30px;
      position: relative;
      margin: 12px 0 24px;
      width: 100%;
      &::before, &::after {
        @include p;
        width: 20px;
        height: 20px;
        background-color: #f1f1f1;
        top: -2px;
        left: 0;
        @include tr; }
      &::after {
        background: url('../img/sprite.svg#check') no-repeat center;
        background-size: 14px;
        opacity: 0; }
      &.active {
        &::after {
          opacity: 1; } }
      a {
        color: $basic;
        border-bottom: 1px solid rgba($basic, .5);
        &:hover {
          border-bottom-color: transparent; } } }
    &__button {
      width: 230px;
      height: 60px;
      margin: auto; } }
  &_thanks {
    text-align: center; } }

.wow {
  visibility: hidden; }

.breadcrumbs {
  padding: 15px 0;
  background: #f3f3f3;
  a {
    color: $basic;
    border-bottom: 1px solid rgba($basic, .5);
    &:hover {
      border-bottom-color: transparent; } } }

.inner {
  .content {
    padding: 30px 0 0;
    &_padding {
      padding-bottom: 60px; } }
  .container {
    & > p:not(.subheader) {
      margin: 15px 0; }
    & > .subheader {
      margin-bottom: 20px; }
    & > h1 {
      text-align: center; } }

  .cta {
    margin: 50px 0;
    &:last-child {
      margin: 50px 0 0; }
    &__header {
      margin-bottom: 15px; }
    &__header, &__subheader {
      max-width: 100%;
      text-align: center; }
    &__image {
      display: none !important; }
    &__button {
      margin: auto; } }

  .catalog {
    &-items {
      margin-bottom: 50px; } }

  .about__text {
    max-width: 100%; }

  .contacts {
    margin-bottom: 60px;
    &__item {
      margin-bottom: 10px;
      position: relative;
      padding-left: 40px;
      &::before {
        @include p;
        top: 0;
        left: 0;
        border-radius: 50%;
        background-color: $green;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 14px;
        width: 30px;
        height: 30px; }
      &:last-child {
        margin-bottom: 0; }
      &_address {
        &::before {
          background-image: url('../img/sprite.svg#location-white'); } }
      &_phone {
        &::before {
          background-image: url('../img/sprite.svg#phone'); } }
      &_email {
        &::before {
          background-image: url('../img/sprite.svg#email2'); } } } } }

.one-item {
  margin: 30px 0;
  .item {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    flex-direction: row-reverse;
    &__image {
      width: 50%; }
    &__text {
      width: 50%; }
    .projects__bottom {
      margin-top: 20px; } }
  &.reversed {
    .item {
      &:nth-child(even) {
        flex-direction: row; } } } }

.two-items {
  grid-template-columns: repeat(2, 1fr); }

.three-items {
  grid-template-columns: repeat(3, 1fr); }

.four-items {
  grid-template-columns: repeat(4, 1fr); }

.items {
  display: grid;
  grid-gap: 30px;
  margin: 30px 0;
  .header {
    font-size: 20px;
    margin-bottom: 10px; } }

.price-table {
  border-collapse: collapse;
  margin: 30px 0;
  td {
    border: 1px solid #eee;
    padding: 10px 15px;
    vertical-align: top;
    &:first-child {
      width: calc(100% - 340px); }
    &:nth-child(2) {
      width: 170px; }
    &:nth-child(3) {
      width: 170px; } }
  thead {
    td {
      background: #eee; } } }
