.owl-nav button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 45px;
  height: 45px;
  border-radius: 50%;
  z-index: 10;
  cursor: pointer;
  color: #ffffff !important;
  text-align: center;
  line-height: 45px;
  font-size: 19px !important;
  i {
    position: absolute;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 30; }
  &.disabled {
    pointer-events: none; } }
.owl-prev {
  left: -30px;
  i {
    left: 46%; } }
.owl-next {
  right: -30px;
  i {
    left: 54%; } }

.flex {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start; }

.jcsb {
  justify-content: space-between; }

.jcc {
  justify-content: center; }

.aic {
  align-items: center; }

.text-center {
  text-align: center; }
